import React, { useState } from 'react';
import { Hidden, Drawer, Typography, AppBar, IconButton, Toolbar, CssBaseline, Card, CardContent, TextField, CardActions, Button } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu'

import useStyles from './dash_styles';

import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import DayjsUtils from '@date-io/dayjs';
import MenuDrawer from './components/Dashboard/Drawer/MenuDrawer';
import CreateEvent from './components/Dashboard/CreateEvent/CreateEvent';
import ManageEvents from './components/Dashboard/Management/ManageEvents';
import News from './components/Dashboard/News/News';
import { Redirect, Route, Switch, useRouteMatch } from 'react-router';
import NotFound from './components/Loading/NotFound';
import { InputOutlined } from '@material-ui/icons';
import { useDispatch } from 'react-redux';
import { login } from './actions/events';

const Dashboard = () => {
    const classes = useStyles();
    const [ key, setKey ] = useState(""); //fe89ew7tweeolj39y8ddiuw7d
    const [ valid, setValid ] = useState(false);
    const [ error, setError ] = useState(false);
    const dispatch = useDispatch();
    const { path } = useRouteMatch();

    const [mobileOpen, setMobileOpen] = React.useState(false);

    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };

    const handleLogin = () => {
        dispatch(login(key, callback));
    }

    const callback = (res) => {
        console.log(res);
        if (res.status === 200) { setValid(true); setError(false); }
        else { setError(true); setValid(false); console.log("error") }
    }

    const logout = () => {
        setKey("");
        setValid(false);
        setError(false);
    }

    const container = window !== undefined ? () => document.body : undefined;

    return (
        valid ? (
        <div className={classes.root} >
            <CssBaseline />
            <AppBar position="fixed" className={classes.appBar}>
                <Toolbar className={classes.tools}>
                <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    edge="start"
                    onClick={handleDrawerToggle}
                    className={classes.menuButton}
                >
                    <MenuIcon />
                </IconButton>
                <Typography variant="h6" noWrap>
                    Vilkkoni Control Panel
                </Typography>

                </Toolbar>
            </AppBar>
            <nav className={classes.drawer} >
                <Hidden mdUp implementation="js">
                    <Drawer
                        container={container}
                        variant="temporary"
                        anchor={'left'}
                        open={mobileOpen}
                        onClose={handleDrawerToggle}
                        classes={{
                        paper: classes.drawerPaper,
                        }}
                        ModalProps={{
                        keepMounted: true, // Better open performance on mobile.
                        }}
                    >
                    <MenuDrawer logout={logout} />
                    </Drawer>
                </Hidden>
                <Hidden smDown implementation="js">
                    <Drawer
                        classes={{
                        paper: classes.drawerPaper,
                        }}
                        variant="permanent"
                        open
                    >
                    <MenuDrawer logout={logout} />
                    </Drawer>
                </Hidden>
            </nav>
            <main className={classes.content}>
                <div className={classes.toolbar} />
                <MuiPickersUtilsProvider utils={DayjsUtils}>
                    <Switch>
                        <Route exact path="/" >
                            <ManageEvents api_key={key} />
                        </Route>
                        <Route path="/create">
                            <CreateEvent api_key={key} />
                        </Route>
                        <Route path="/news">
                            <News api_key={key} />
                        </Route>
                        <Route path="/statistics">
                            <Typography variant="h5" color="secondary">
                                WORK IN PROGRESS
                            </Typography>
                        </Route>
                        <Route path="*">
                            <Redirect to="/" />
                        </Route>
                    </Switch>
                </MuiPickersUtilsProvider>
            </main>
        </div> 
        ) : (
            <div className={classes.loginContainer}>
                <Card className={classes.loginCard}>
                    <Typography>
                        Enter credentials
                    </Typography>
                    <CardContent>
                        <TextField variant="outlined" value={key} onChange={(e) => setKey(e.target.value)} error={error} />
                    </CardContent>
                    <CardActions>
                        <Button variant="contained" color="primary" onClick={handleLogin} >
                            Log in &nbsp;
                            <InputOutlined />
                        </Button>
                    </CardActions>
                </Card>
            </div>
        )
    );
}

export default Dashboard;